import loadingVue from './loading.vue';

let loadingClass = {
	initLoading(vue) {
		let loadingCons = vue.extend(loadingVue);
		// console.dir(loadingCons)
		let loaded = null;
		if (!vue.prototype.$fivexu) vue.prototype.$fivexu = {};
		vue.prototype.$fivexu.loading = {
			show: function (options = {}) {
				return new Promise((resolve, reject) => {
					if (loaded !== null) this.hide();
					let container = document.createElement('div');
					document.body.appendChild(container);
					loaded = new loadingCons({
						el: container,
						propsData: Object.assign(options, {showLoading: true})
					});
					if (options.isCancel) {
						window.bus.$on('cancel', () => {
							resolve('cancel')
						})
					}
				})
			},
			hide: function () {
				if (loaded !== null) {
					document.body.removeChild(loaded.$el);
					loaded = null;
				}
			}
		}
	}
};

export default loadingClass;
