import Vue from "vue";
import VueRouter from "vue-router";
import _2048 from "../views/2048.vue";
import Snake from "../views/snake.vue";
import Sokoban from "../views/sokoban.vue";
import FiveChess from "../views/fiveChess.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "*",
    redirect: "/sokoban"
  },
  {
    path: "/sokoban",
    name: "sokoban",
    component: Sokoban
  },
  {
    path: "/snake",
    name: "snake",
    component: Snake
  },
  {
    path: "/fiveChess",
    name: "fiveChess",
    component: FiveChess
  },
  {
    path: "/2048",
    name: "2048",
    component: _2048
  }
];

const router = new VueRouter({
  routes
});

export default router;
