<template>
  <div class="home">
    <div class="box">
      <div class="wrapper">
        <ul>
          <li v-for="(item,itemIndex) in gameList" :key="itemIndex">
            <span v-for="(child,childIndex) in item" :key="childIndex">
              <em
                v-if="child.num!==0"
                :style="{left:`${child.left}px`,top:`${child.top}px`}"
                :class="`_${child.num}`"
                :key="childIndex"
              >{{child.num}}</em>
            </span>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data() {
    return {
      gameList: [[], [], [], []]
    }
  },
  methods: {
    initBox() {
      this.setPosition()
    },
    randomPiece() {
      const x = Math.floor(Math.random() * 4)
      const y = Math.floor(Math.random() * 4)
      if (this.gameList[x][y].num === 0) {
        this.gameList[x][y].num = 2
      } else {
        this.randomPiece()
      }
    },
    setPosition() {
      let arr = Array.from(new Array(16))
      this.gameList = [[], [], [], []]
      arr.forEach((item, index) => {
        let ind = parseInt(Math.floor(index / 4))
        this.gameList[ind].push({
          left: (index % 4) * 110,
          top: Math.floor(index / 4) * 110,
          num: 0,
          isAdd: false
        })
      })
      this.randomPiece()
      this.randomPiece()
    },
    keydown(ev) {
      this.$warning('建设中')
      return
      this.gameList.forEach((item, itemIndex) => {
        this.run()
      })
    },
    row(arr) {
      for (let i = arr.length - 1; i >= 0; i--) {
        if (arr[i].num !== 0) {
          if (arr[i + 1]) arr[i].left = arr[i + 1].left
        }
      }
      this.row(arr)
    },
    isZero(arr) {},
    run() {
      this.gameList.forEach(item => {
        this.row(item)
      })
    }
  },
  mounted() {
    this.initBox()
    window.addEventListener('keydown', this.keydown, true)
  },
  destroyed() {
    window.removeEventListener('keydown', this.keydown, true)
  }
}
</script>

