<template>
	<div @dragover.prevent.stop="" @drop.prevent.stop="" class="loading"
	     :style="{backgroundColor:bgColor}" v-if="showLoading">
		<div class="content" ref="content" :style="{color:textColor}">
			<p ref="text" v-if="text!==''&&textPosition==='top'" v-html="text"></p>
			<svg class="fivexu_loading_svg" viewBox="25 25 50 50">
				<circle class="fivexu_loading_circle" cx="50" cy="50" r="20" fill="none"/>
			</svg>
			<p ref="text" v-if="text!==''&&textPosition==='bottom'" v-html="text"></p>
			<button v-if="isCancel" @click="cancel">{{!isCancelClick?'取消':'取消中..'}}</button>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'loadingClass',
		props: {
			showLoading: {
				type: Boolean,
				default: false
			},
			text: {
				type: String,
				default: ''
			},
			textPosition: {
				type: String,
				default: 'bottom'
			},
			textColor: {
				type: String,
				default: '#fff'
			},
			bgColor: {
				type: String,
				default: 'rgba(0, 0, 0, .5)'
			},
			isCancel: {
				type: Boolean,
				default: false
			}
		},
		data() {
			return {
				isCancelClick: false
			}
		},
		methods: {
			initContent() {
				if (!this.$refs.content) return;
				this.$refs.content.style.height = `${(this.$refs.text ? this.$refs.text.offsetHeight : 0) + 50}px`;
			},
			cancel() {
				if (this.isCancelClick) return;
				this.isCancelClick = true;
				window.bus.$emit('cancel');
			}
		},
		mounted() {
			this.$nextTick(() => {
				this.initContent();
			})
		}
	}
</script>

<style scoped lang="less">
	@import "../util/style/common";

	.loading {
		width: 100%;
		height: 100%;
		position: fixed;
		left: 0;
		top: 0;
		z-index: @z-index-base;
		.content {
			width: fit-content;
			height: fit-content;
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			margin: auto;
			text-align: center;
			p {
				width: 100%;
				line-height: 20px;
				font-size: 14px;
			}
			img {
				width: 128px;
				height: 72px;
				float: left;
			}
		}
	}
</style>
