import Vue from "vue";
import five from "./fivexuUI/index";
Vue.use(five);
const vue = new Vue();

Vue.prototype.$warning = text => {
  vue.$fivexu.alert.show({ mesType: "warning", message: text });
};
Vue.prototype.$success = text => {
  vue.$fivexu.alert.show({ mesType: "success", message: text });
};
