<template>
	<div class="fivexu_modal_wrapper">
		<div class="fivexu_modal_bg" ref="modalBg" @click.stop.prevent="cancelModal"></div>
		<div class="modal" ref="modal" :style="{width:`${Number(width)}px`}" :class="{ac:show}">
			<!-- title -->
			<div class="title" v-if="!hasSlot('title')&&isTitle">
				<h6>{{title}}</h6>
				<span @click.stop="cancel"><i class="iconfont icon-close"></i></span>
			</div>
			<slot v-else name="title"></slot>
			<!-- title -->
			<!-- content -->
			<div class="content">
				<slot name="default"></slot>
			</div>
			<!-- content -->
			<!-- footer -->
			<div class="footer" v-if="!hasSlot('footer')&&isFooter">
				<!--<fx-button @clickEvt="cancel" type="default">取消</fx-button>-->
				<div class="btn_ac" @click="define">确定</div>
			</div>
			<slot name="footer" v-else></slot>
			<!-- footer -->
		</div>
	</div>
</template>

<script>
	import Button from '../form/button.vue';
	import Global from '../global/global.vue';

	export default {
		extends: Global,
		name: 'modal',
		props: {
			width: {
				type: Number | String,
				default: 400
			},
			title: {
				type: String,
				default: '提示'
			},
			isFooter: {
				type: Boolean,
				default: true
			},
			isTitle: {
				type: Boolean,
				default: true
			},
			isModalClose: {
				type: Boolean,
				default: true
			}
		},
		data() {
			return {
				show: false
			}
		},
		methods: {
			_initStyle() {
				let modalBg = document.getElementsByClassName('fivexu_modal_bg');
				this.$refs.modal.style.zIndex = modalBg.length + 1000;
				this.$refs.modalBg.style.zIndex = modalBg.length + 1000;
				if (modalBg.length > 1) this.$refs.modalBg.style.backgroundColor = 'rgba(0, 0, 0, .1)';
			},
			cancelModal() {
				if (!this.isModalClose) return;
				this.$emit('cancel', null);
			},
			cancel() {
				this.$emit('cancel', null);
			},
			define() {
				this.$emit('define', null);
			},
			keyDown(ev) {
				ev = ev || event;
				if (ev.keyCode === 27) this.cancel();
			}
		},
		mounted() {
			this.$nextTick(() => {
				window.addEventListener('keydown', this.keyDown.bind(this));
				this._initStyle();
				setTimeout(() => {
					this.show = true;
				}, 30)
			})
		},
		beforeDestroy() {
			window.removeEventListener('keydown', this.keyDown.bind(this));
		},
		destroyed() {
			window.removeEventListener('keydown', this.keyDown.bind(this));
		},
		components: {
			'fx-button': Button
		}
	}
</script>

<style scoped lang="less">
	@import "../util/style/common.less";

	@keyframes bottom {
		from {
			transform: translateY(-30px);
			opacity: 0;
		}
		to {
			transform: translateY(0);
			opacity: 1;
		}
	}

	@keyframes opacity {
		from {
			opacity: 0;
		}
		to {
			opacity: 1;
		}
	}

	.fivexu_modal_wrapper {
		.fivexu_modal_bg {
			width: 100%;
			height: 100%;
			position: fixed;
			z-index: @z-index-nav;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background-color: rgba(0, 0, 0, .5);
			animation: opacity 0.3s;
			animation-fill-mode: forwards;
			opacity: 0;
		}
		.modal {
			width: fit-content;
			height: fit-content;
			border-radius: 13px;
			box-shadow: 0 0 20px 4px rgba(0, 0, 0, 0.2);
			animation: bottom 0.3s;
			animation-fill-mode: forwards;
			background-color: @white;
			position: fixed;
			z-index: @z-index-base;
			left: 0;
			right: 0;
			bottom: 0;
			top: 0;
			margin: auto;
			box-sizing: border-box;
			overflow: hidden;
			.transform(scale(0.8));
			.transition(scale 0.08s ease-out);
			&.ac {
				opacity: 1;
				.transform(scale(1));
			}
			> .title {
				text-align: center;
				background: linear-gradient(90deg, rgba(50, 79, 218, 1) 0%, rgba(84, 93, 225, 1) 100%);
				height: 0.46rem;
				line-height: 0.46rem;
				font-size: 20px;
				font-weight: 500;
				color: #fff;
				display: flex;
				flex-wrap: nowrap;
				justify-content: space-between;
				padding: 0 20px;
				box-sizing: border-box;
				position: relative;
				h6 {
					width: 100%;
				}
				> span {
					position: absolute;
					cursor: pointer;
					right: 20px;
				}
			}
			.footer {
				padding-top: 10px;
				text-align: center;
			}
		}
		.btn_ac {
			width: 110px;
			height: 36px;
			line-height: 36px;
			float: right;
			margin:0 45px 20px 0;
		}
	}
</style>
