<template>
  <div class="snake">
    <div class="box">
      <h2>蛇长度{{snakeCount}}</h2>
      <canvas ref="canvasDom"></canvas>
      <div class="btn">
        <fx-select :disable="timer!==null" :data="diffData" v-model="difficult"></fx-select>
        <fx-button type="default" @clickEvt="start">{{timer===null?'开始':'重置'}}</fx-button>
        <fx-button type="default" @clickEvt="isGrid=!isGrid">{{!isGrid?'格子':'无格'}}</fx-button>
        <fx-button type="default" @clickEvt="isAI=!isAI">{{!isAI?'自动':'手动'}}</fx-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'snake',
  data() {
    return {
      context: null,
      // 横纵轴 条数
      lattice: { w: 80, h: 80 },
      // 每条轴间距
      row: 0,
      // 每条轴间距
      col: 0,
      // 蛇长度
      snakeCount: 6,
      // 蛇数组
      snake: [],
      // 食物位置
      foodInfo: { x: 0, y: 0 },
      // 方向
      path: 'toRight',
      // 当前前进方向
      currentPath: 39,
      // 速度
      time: 10,
      // 定时器
      timer: null,
      // 是否自动跑
      isAI: false,
      // 是否需要网格
      isGrid: true,
      // 难度
      difficult: 200,
      // 难度列表
      diffData: [
        { label: '简单', value: 200 },
        { label: '正常', value: 100 },
        { label: '快', value: 50 },
        { label: '极快', value: 30 }
      ]
    }
  },
  methods: {
    // 初始化
    init() {
      this.context = this.$refs.canvasDom.getContext('2d')
      const canvas = this.$refs.canvasDom
      this.width = canvas.clientWidth
      this.height = canvas.clientHeight
      this.row = this.width / this.lattice.w
      this.col = this.height / this.lattice.h
      canvas.width = this.width
      canvas.height = this.height
      this.context.fillRect(this.foodInfo.x, this.foodInfo.y, this.row, this.col)
      this.addSnake()
      this.addFood(this.context)
      this.drawLine(this.context)
      clearInterval(this.timer)
      this.timer = setInterval(() => {
        this.move(this.context, this.path)
      }, this.difficult)
    },
    // 划线
    drawLine(context = this.context) {
      // 画row
      for (let i = 0; i < this.lattice.w; i++) {
        context.strokeStyle = this.isGrid ? '#ccc' : '#fff'
        context.lineWidth = 1
        context.beginPath()
        context.moveTo(0, this.row * i)
        context.lineTo(this.width, this.row * i)
        context.closePath()
        context.stroke()
      }
      // 画col
      for (let i = 0; i < this.lattice.h; i++) {
        context.strokeStyle = this.isGrid ? '#ccc' : '#fff'
        context.lineWidth = 1
        context.beginPath()
        context.moveTo(this.col * i, 0)
        context.lineTo(this.col * i, this.height)
        context.closePath()
        context.stroke()
      }
      // 画蛇
      for (let i = 0; i < this.snakeCount; i++) {
        context.fillStyle = '#000'
        if (i === this.snakeCount - 1) context.fillStyle = '#f60'
        context.fillRect(this.snake[i].x, this.snake[i].y, this.row, this.col)
      }
      // 画食物
      context.beginPath()
      context.fillStyle = 'black'
      context.fillRect(this.foodInfo.x, this.foodInfo.y, this.row, this.col)
      context.closePath()
      context.fill()
    },
    // 添加食物
    addFood(context) {
      this.foodInfo = {
        x: Math.floor(Math.random() * this.lattice.w) * this.row,
        y: Math.floor(Math.random() * this.lattice.h) * this.col
      }
      let off = this.snake.some(item => {
        return item.x === this.foodInfo.x && item.y === this.foodInfo.y
      })
      if (off) this.addFood(context)
    },
    // 添加蛇长度
    addSnake() {
      let y = Math.floor(Math.random() * this.lattice.h) * this.col
      let x = Math.floor(Math.random() * this.lattice.w) * this.row
      if (Math.random() > 0.5) {
        this.path = 'toRight'
        this.currentPath = 37
        for (let i = 0; i < this.snakeCount; i++) {
          this.snake.push({ x: i * this.row, y })
        }
      } else {
        this.path = 'toBottom'
        this.currentPath = 38
        for (let i = 0; i < this.snakeCount; i++) {
          this.snake.push({ x, y: i * this.col })
        }
      }
    },
    // 移动
    move(context, path) {
      const x = this.snake[this.snakeCount - 1].x
      const y = this.snake[this.snakeCount - 1].y
      switch (path) {
        case 'toLeft':
          if (this.isAI && this.AI(x - this.row, y)) return
          this.snake.push({
            x: x - this.row,
            y: y
          })
          break
        case 'toTop':
          if (this.AI(x, y - this.col)) return
          this.snake.push({
            x: x,
            y: y - this.col
          })
          break
        case 'toRight':
          if (this.isAI && this.AI(x + this.row, y)) return
          this.snake.push({
            x: x + this.row,
            y: y
          })
          break
        case 'toBottom':
          if (this.isAI && this.AI(x, y + this.col)) return
          this.snake.push({
            x: x,
            y: y + this.col
          })
          break
        default:
          break
      }
      this.snake.shift()
      context.clearRect(0, 0, this.width, this.height)
      this.isEat()
      this.gameOver()
      this.drawLine(context)
    },
    // 吃食物
    isEat() {
      const x = this.snake[this.snake.length - 1].x === this.foodInfo.x
      const y = this.snake[this.snake.length - 1].y === this.foodInfo.y
      if (x && y) {
        this.addFood()
        this.snakeCount++
        this.snake.unshift({ x: -this.row, y: -this.col })
      }
    },
    // 自动
    AI(x, y) {
      let off = this.snake.some(item => {
        return item.x === x && item.y === y
      })
      if (off) {
        if (this.path === 'toTop' || this.path === 'toBottom') {
          if (x !== 0) this.path = 'toLeft'
          if (x === 0) this.path = 'toRight'
          return
        }
        if (this.path === 'toLeft' || this.path === 'toRight') {
          if (y !== 0) this.path = 'toTop'
          if (y === 0) this.path = 'toBottom'
          return
        }
        return
      }
      if (x < 0 && y < 0) {
        if (this.path === 'toTop') this.path = 'toRight'
        if (this.path === 'toLeft') this.path = 'toBottom'
        return
      }
      if (x < 0) {
        if (y !== 0) {
          if (y <= this.foodInfo.y && (this.path === 'toLeft' || this.path === 'toRight'))
            this.path = 'toBottom'
          if (y > this.foodInfo.y && (this.path === 'toLeft' || this.path === 'toRight'))
            this.path = 'toTop'
        }
        if (y === 0) this.path = 'toBottom'
        return true
      }
      if (x > this.width - this.row) {
        if (y !== 0) {
          if (y <= this.foodInfo.y && (this.path === 'toLeft' || this.path === 'toRight'))
            this.path = 'toBottom'
          if (y > this.foodInfo.y && (this.path === 'toLeft' || this.path === 'toRight'))
            this.path = 'toTop'
        }
        if (y === 0) this.path = 'toBottom'
        return true
      }
      if (y < 0) {
        if (x !== 0) {
          if (x <= this.foodInfo.x && (this.path === 'toTop' || this.path === 'toBottom'))
            this.path = 'toRight'
          if (x > this.foodInfo.x && (this.path === 'toTop' || this.path === 'toBottom'))
            this.path = 'toLeft'
        }
        if (x === 0) this.path = 'toRight'
        return true
      }
      if (y > this.height - this.col) {
        if (x !== 0) {
          if (x <= this.foodInfo.x && (this.path === 'toTop' || this.path === 'toBottom'))
            this.path = 'toRight'
          if (x > this.foodInfo.x && (this.path === 'toTop' || this.path === 'toBottom'))
            this.path = 'toLeft'
        }
        if (x === 0) this.path = 'toRight'
        return true
      }
      if (x === this.foodInfo.x) {
        if (y <= this.foodInfo.y && (this.path === 'toLeft' || this.path === 'toRight'))
          this.path = 'toBottom'
        if (y > this.foodInfo.y && (this.path === 'toLeft' || this.path === 'toRight'))
          this.path = 'toTop'
        return false
      }
      if (y === this.foodInfo.y) {
        if (x <= this.foodInfo.x && (this.path === 'toTop' || this.path === 'toBottom'))
          this.path = 'toRight'
        if (x > this.foodInfo.x && (this.path === 'toTop' || this.path === 'toBottom'))
          this.path = 'toLeft'
        return false
      }
      return false
      // x <= 0 && y !== 0 ? path = 'toTop' : path = 'toBottom';
      // x >= width && y !== 0 ? path = 'toTop' : path = 'toBottom';
    },
    // 按键
    keyDown(ev) {
      ev = ev || event
      if (ev.keyCode === this.currentPath) return
      switch (Number(ev.keyCode)) {
        case 37:
          this.path = 'toLeft'
          this.currentPath = 39
          break
        case 38:
          this.path = 'toTop'
          this.currentPath = 40
          break
        case 39:
          this.path = 'toRight'
          this.currentPath = 37
          break
        case 40:
          this.path = 'toBottom'
          this.currentPath = 38
          break
        default:
          break
      }
    },
    // 游戏结束
    gameOver() {
      let len = this.snake.length - 1
      let off = this.snake.some((item, index) => {
        return index !== len && this.snake[len].x === item.x && this.snake[len].y === item.y
      })
      if (
        this.snake[len].x < 0 ||
        this.snake[len].x >= this.width ||
        this.snake[len].y < 0 ||
        this.snake[len].y >= this.height
      ) {
        clearInterval(this.timer)
        this.timer = null
        this.$warning('撞墙了')
        return true
      }
      if (off) {
        clearInterval(this.timer)
        this.timer = null
        this.$warning('撞自己了')
        return true
      }
      return false
    },
    // 重置
    clear() {
      clearInterval(this.timer)
      this.timer = null
      this.path = 'toRight'
      this.snakeCount = 6
      this.snake = []
      this.foodInfo = { x: 0, y: 0 }
      this.currentPath = 39
      this.isAI = false
    },
    start() {
      this.clear()
      this.init()
    }
  },
  mounted() {
    this.$nextTick(() => {
      window.addEventListener('keydown', this.keyDown, true)
    })
  },
  destroyed() {
    this.clear()
    window.removeEventListener('keydown', this.keyDown, true)
  }
}
</script>

<style lang="less" scoped>
.snake {
  width: 100%;
  height: 100%;
  position: relative;
  > .box {
    width: fit-content;
    height: fit-content;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    > h2 {
      text-align: center;
    }
    > canvas {
      display: block;
      height: 600px;
      width: 600px;
      border-radius: 4px;
      background-color: #fff;
      box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.5);
    }
    > .btn {
      padding-top: 20px;
      // text-align: center;
      /deep/ .select_wrapper {
        width: 60px;
      }
    }
  }
}
</style>